import { useEffect, useState } from 'react';
import noteService from './services/notes';
import Note from './components/Note';
import Form from './components/Form';

const App = () => {
	const [notes, setNotes] = useState([]);
	const [noteText, setNoteText] = useState('');
	const [showAll, setShowAll] = useState(true);

	useEffect(() => {
		setTimeout(() => {
			noteService.getAll().then((data) => setNotes(data));
		}, 1000);
	}, []);

	const toggleImportance = (id) => {
		const note = notes.find((note) => note.id === id);
		const changedNote = { ...note, important: !note.important };

		noteService
			.updateNote(id, changedNote)
			.then((data) => {
				setNotes(notes.map((note) => (note.id !== id ? note : data)));
			})
			.catch((error) => {
				alert(`The note with id ${id} was not found`);
			});
		setNotes(notes.filter((note) => note.id !== id));
	};

	const handleSubmit = (e) => {
		e.preventDefault();

		const newNote = {
			content: noteText,
			important: Math.random() < 0.5,
		};

		noteService.createNote(newNote).then((data) => {
			setNotes([...notes, data]);
			setNoteText('');
		});
	};

	return (
		<div style={{ margin: `2rem` }}>
			<h1>Notes</h1>
			<button
				style={{ marginBottom: 20 }}
				type='botton'
				onClick={() => setShowAll(!showAll)}
			>
				Show {showAll ? 'Important' : 'All'}
			</button>
			<ul style={{ listStyle: `none` }}>
				{notes.length === 0 && <p>Loading notes...</p>}
				{showAll &&
					notes.map((note) => (
						<Note
							key={note.id}
							toggleImportance={() => toggleImportance(note.id)}
							note={note}
						/>
					))}
				{!showAll &&
					notes
						.filter((note) => note.important)
						.map((note) => (
							<Note
								key={note.id}
								toggleImportance={() => toggleImportance(note.id)}
								note={note}
							/>
						))}
			</ul>
			<Form
				handleSubmit={handleSubmit}
				noteText={noteText}
				setNoteText={setNoteText}
			/>
		</div>
	);
};

export default App;
